<template>
    <div class="admin-products">
      <h1>Управление товарами</h1>
  
      <a-form layout="inline" @submit.prevent>
        <a-form-item>
          <a-input v-model:value="form.name" placeholder="Название товара" />
        </a-form-item>
  
        <a-form-item>
          <a-select v-model:value="form.category" placeholder="Категория">
            <a-select-option value="iphones">Айфоны</a-select-option>
            <a-select-option value="ipads">Айпады</a-select-option>
            <a-select-option value="accessories">Аксессуары</a-select-option>
          </a-select>
        </a-form-item>
  
        <a-form-item>
          <a-input-number v-model:value="form.price" placeholder="Цена" />
        </a-form-item>
  
        <a-form-item>
          <a-input-number v-model:value="form.sold" placeholder="Продано" />
        </a-form-item>
  
        <a-form-item>
          <a-input-number v-model:value="form.stock" placeholder="Остаток" />
        </a-form-item>
  
        <a-form-item>
          <input type="file" @change="handleFileUpload" />
        </a-form-item>
  
        <a-form-item>
          <a-button type="primary" @click="addOrUpdateProduct">
            {{ editingProduct ? 'Обновить' : 'Добавить' }}
          </a-button>
        </a-form-item>
      </a-form>
  
      <a-table :columns="columns" :data-source="products" rowKey="id" bordered>
        <template #bodyCell="{ column, record }">
          <template v-if="column.key === 'image'">
            <img :src="record.image" style="width: 60px;" v-if="record.image" />
          </template>
          <template v-else-if="column.key === 'actions'">
            <a-space>
              <a-button type="link" @click="editProduct(record)">Редактировать</a-button>
              <a-button type="link" danger @click="deleteProduct(record.id)">Удалить</a-button>
            </a-space>
          </template>
        </template>
      </a-table>
    </div>
  </template>
  
  <script>
  export default {
    name: 'AdminProducts',
    data() {
      return {
        form: {
          name: '',
          category: '',
          price: null,
          image: '',
          sold: 0,
          stock: 0
        },
        products: [],
        editingProduct: null,
        columns: [
          { title: 'Название', dataIndex: 'name', key: 'name' },
          { title: 'Категория', dataIndex: 'category', key: 'category' },
          { title: 'Цена', dataIndex: 'price', key: 'price' },
          { title: 'Продано', dataIndex: 'sold', key: 'sold' },
          { title: 'Остаток', dataIndex: 'stock', key: 'stock' },
          { title: 'Изображение', dataIndex: 'image', key: 'image' },
          { title: 'Действия', key: 'actions' }
        ]
      }
    },
    methods: {
      handleFileUpload(event) {
        const file = event.target.files[0];
        if (file) {
          const reader = new FileReader();
          reader.onload = (e) => {
            this.form.image = e.target.result;
          };
          reader.readAsDataURL(file);
        }
      },
      addOrUpdateProduct() {
        if (this.editingProduct) {
          Object.assign(this.editingProduct, this.form);
          this.editingProduct = null;
        } else {
          this.products.push({ ...this.form, id: Date.now() });
        }
        this.resetForm();
      },
      editProduct(product) {
        this.editingProduct = product;
        this.form = { ...product };
      },
      deleteProduct(id) {
        this.products = this.products.filter(p => p.id !== id);
      },
      resetForm() {
        this.form = {
          name: '',
          category: '',
          price: null,
          image: '',
          sold: 0,
          stock: 0
        };
      }
    }
  }
  </script>
  
  <style scoped>
  .admin-products {
    padding: 20px;
  }
  img {
    border-radius: 4px;
  }
  </style>
  
  